:root {
  --transition: 1s;
}

.my__container {
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 30px;
  display: flex;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  box-sizing: border-box;
  cursor: pointer;
  background-color: #4b48db;
  border: .6px solid #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #fff;
  border-radius: 20px;
  transition: all 1s;
  position: absolute;
  bottom: 1px;
  right: 1px;
}

input:checked + .slider {
  background-color: #fff;
  border: .6px solid #4b48db;
}

input:focus + .slider {
  box-shadow: 0 0 10px #4b48db;
}

input:checked + .slider:before {
  background-color: #4b48db;
  transform: translateX(-20px);
}

.slider.round {
  border-radius: 20px;
}

svg {
  fill: currentColor;
}

.toggle__box {
  border: 1px solid red;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.toggle__box__large__size {
  width: 148px;
  height: 24px;
}

.toggle__box__small__size {
  width: 98px;
  height: 21px;
}

.input__plase {
  position: relative;
}

.input__plase input {
  width: 213px;
  height: 31px;
  border-radius: 20px;
  padding-left: 40px;
}

.input__plase div {
  position: absolute;
  top: 7px;
  left: 12px;
}

.search__size {
  border-radius: 20px;
  padding-left: 32px;
}

.light__theme {
  color: #111321;
  transition: var(--transition);
  background-color: #f4f4f4;
}

.dark__theme {
  color: #f4f4f4;
  transition: var(--transition);
  background-color: #2e2e2e;
}

.svg__light__theme {
  fill: #4b48db;
  transition: var(--transition);
}

.svg__dark__theme {
  fill: #5f6775;
  transition: var(--transition);
}

.input__seatch__light__theme {
  color: #111321;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #111321;
}

.input__seatch__dark__theme {
  color: #757575;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #757575;
}

.svg__search__light__theme {
  color: #111321;
}

.svg__search__dark__theme {
  color: #757575;
}

.menu__burger__light__theme {
  stroke: red;
}

